import React, { useState, useEffect } from 'react';
import '../styles/shelter.css';

function ShelterSection({ backgroundImage, mainText, subText }) {
    const [mousePosition, setMousePosition] = useState({ x: 0, y: 0 });
    const [maskSize, setMaskSize] = useState(50); // 마스크 크기 초기값

    useEffect(() => {
        const sections = document.querySelectorAll('.shelter');
        
        const observer = new IntersectionObserver(
            (entries) => {
                entries.forEach((entry) => {
                    if (entry.isIntersecting) {
                        entry.target.classList.add('visible');
                    } else {
                        entry.target.classList.remove('visible');
                    }
                });
            },
            { threshold: 0.5 } // 섹션이 50% 보일 때 애니메이션 시작
        );

        sections.forEach((section) => {
            observer.observe(section);
        });

        return () => {
            sections.forEach((section) => {
                observer.unobserve(section);
            });
        };
    }, []);

    // 마우스 이동 시 원형 마스크 크기와 위치 업데이트
    useEffect(() => {
        const handleMouseMove = (e) => {
            const x = e.clientX;
            const y = e.clientY;
            setMousePosition({ x, y });

            // 화면 중앙과의 거리에 따라 마스크 크기 계산
            const middleY = window.innerHeight / 2;
            const distanceFromMiddle = Math.abs(middleY - y);
            const newSize = Math.max(100, 450 - (distanceFromMiddle / middleY) * 150);
            setMaskSize(newSize); // 마스크 크기 설정
        };

        window.addEventListener('mousemove', handleMouseMove);

        return () => {
            window.removeEventListener('mousemove', handleMouseMove);
        };
    }, []);

    return (
        <div
            className="shelter"
            style={{
                backgroundImage: `url(${backgroundImage})`, // 배경 이미지 설정
                backgroundSize: 'cover',
                backgroundPosition: 'center',
                backgroundRepeat: 'no-repeat',
            }}
        >
            <div
                className="mask"
                style={{
                    left: mousePosition.x - maskSize / 2,
                    top: mousePosition.y - maskSize / 2,
                    width: maskSize,
                    height: maskSize,
                    transition: 'width 0.3s ease, height 0.3s ease',
                }}
            ></div>
            <h1>{mainText}</h1>
            <p>{subText}</p>
        </div>
    );
}

function Shelter() {
    return (
        <div className="shelter-container">
            <ShelterSection 
                backgroundImage="https://github.com/SSuaSea/react-adorable/blob/main/public/image/dog3.jpg?raw=true"
                mainText="언제나 곁을 지켜주는 사랑스러운 나의 동반자"
                subText="The loving companion who always stays by my side"
            />

            <ShelterSection 
                backgroundImage="https://github.com/SSuaSea/react-adorable/blob/main/public/image/dog9.jpg?raw=true"
                mainText="사지말고, 입양하세요"
                subText="유기동물 입양 문화를 알리고 확산시키기 위해 보호소를 개설했습니다"
            />

            <ShelterSection 
                backgroundImage="https://github.com/SSuaSea/react-adorable/blob/main/public/image/dog10.jpg?raw=true"
                mainText="따뜻한 보금자리가 되어주세요"
                subText="보호소에있는 모든 친구들이 여러분의 사랑을 기다리고 있습니다"
            />
        </div>
    );
}

export default Shelter;
