import React, { useContext, useState, useEffect } from 'react';
import axios from 'axios';
import { AuthContext } from '../contexts/AuthContext';
import '../styles/mypage.css';

const Mypage = () => {
    const { token, logout } = useContext(AuthContext);  // AuthContext에서 token 가져오기
    const [userInfo, setUserInfo] = useState({ id: '', name: '', email: '' });
    const [editMode, setEditMode] = useState(false);
    const [newName, setNewName] = useState('');
    const [newEmail, setNewEmail] = useState('');

    useEffect(() => {
        const fetchUserInfo = async () => {
            try {
                const response = await axios.get('http://localhost:5000/mypage', {
                    headers: {
                        Authorization: `Bearer ${token}`
                    }
                });
                console.log('서버 응답:', response.data);  // 서버 응답 확인
                setUserInfo(response.data);
                setNewName(response.data.name);
                setNewEmail(response.data.email);
            } catch (error) {
                if (error.response) {
                    console.error('서버 응답 오류:', error.response.data);
                } else {
                    console.error('Error fetching user info:', error);
                }
                alert('Failed to fetch user information');
            }
        };
    
        if (token) {
            fetchUserInfo();
        } else {
            console.error('Token is missing');
        }
    }, [token]);
    

    const handleUpdate = async () => {
        try {
            await axios.put('http://localhost:5000/mypage', {
                name: newName,
                email: newEmail
            }, {
                headers: {
                    Authorization: `Bearer ${token}`
                }
            });
            setUserInfo({ ...userInfo, name: newName, email: newEmail });
            setEditMode(false);
            alert('Profile updated successfully');
        } catch (error) {
            console.error('Failed to update profile:', error);
            alert('Failed to update profile');
        }
    };

    return (
        <div className="mypage-container">
            <h2>마이페이지</h2>
            <div className="user-info">
                {userInfo.id ? (
                    <>
                        <p><strong>ID:</strong> {userInfo.id}</p>
                        <p><strong>Name:</strong> {userInfo.name}</p>
                        <p><strong>Email:</strong> {userInfo.email}</p>
                        {!editMode ? (
                            <button onClick={() => setEditMode(true)} className="edit-button">수정하기</button>
                        ) : (
                            <>
                                <p>
                                    <strong>Name:</strong>
                                    <input 
                                        type="text"
                                        value={newName}
                                        onChange={(e) => setNewName(e.target.value)}
                                        className="edit-input"
                                    />
                                </p>
                                <p>
                                    <strong>Email:</strong>
                                    <input 
                                        type="email"
                                        value={newEmail}
                                        onChange={(e) => setNewEmail(e.target.value)}
                                        className="edit-input"
                                    />
                                </p>
                                <button onClick={handleUpdate} className="save-button">저장</button>
                                <button onClick={() => setEditMode(false)} className="cancel-button">취소</button>
                            </>
                        )}
                    </>
                ) : (
                    <p>사용자 정보를 불러오는 중입니다...</p>
                )}
            </div>
            <button onClick={logout} className="logout-button">로그아웃</button>
        </div>
    );
};

export default Mypage;
